.contact_container {
    grid-template-columns: repeat(1, max-content);
    justify-content: center;
    
}

.contact_title {
    text-align: center;
    font-size: var(--h3-font-size);
    font-weight: var(--font-medium);
    margin-bottom: var(--mb-1-5);
}



.contact_form {
    width: 450px;
}

.contact_form-div{
    position: relative;
    margin-bottom: var(--mb-2);
    height: 4rem;
}

.contact_form-input{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 2px solid rgba(0, 0, 0, 0.3);
    background: none;
    color: var(--text-color);
    outline: none;
    border-radius: 0.75rem;
    padding: 1.5rem;
    z-index: 1;
}

.contact_form-tag{
    position: absolute;
    top: -0.75rem;
    left: 1.25rem;
    font-size: var(--smaller-font-size);
    padding: 0.25rem;
    background-color: var(--body-color);
    z-index: 10;
}

.contact_form-area {
    height: 11rem;
}

.contact_form-area textarea {
    resize: none;
}


@media screen and (max-width: 992px) {
    .contact_container {
        column-gap: 3rem;
    }
  }
  
  @media screen and (max-width: 768px) {
    .about_container {
        grid-template-columns: 1fr;
        row-gap: 3rem;
    }

    .contact_form {
        margin: 0 auto;
    }
  }
  
  @media screen and (max-width: 576px) {
    .contact_form {
        width: 100%;
    }
  }
  
