.about_container {
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
    column-gap: 4rem;
}

.about_img {
    width: 350px;
    border-radius: 1.5rem;
    justify-self: center;
}

.about_title {
    font-size: var(--small-font-size);
    font-weight: var(--font-medium);
}

.about_subtitle {
    font-size: var(--tiny-font-size);
}

.about_data {
    text-align: justify;
}

.about_description {
    padding: 0 4rem 0 0;
    margin-bottom: var(--mb-2-5);
}

@media screen and (max-width: 992px) {
    .about_container {
        grid-template-columns: 1fr;
        row-gap: 2.5rem;
    }

    .about_img {
        padding: 0.75rem 0.5rem;
    }

    .about_data {
        text-align: center;
    }

    .about_description {
        padding: 0.5rem;
        margin-bottom: 2rem;
    }

  }
  
  
  @media screen and (max-width: 576px) {
    .about_description {
        padding: 0;
    }
  }
  
